import {
  startCase,
  toLower,
  get,
  isArray,
  flatten,
  uniq,
} from 'lodash';
import { CircularProgress } from '@material-ui/core';
import { typeByBrowsers, iconByBrowser } from './BrowserDecorator';
import { typeByOS, iconByOS } from './OSDecorator';
import { IconClock, IconProfile, IconStatusError, IconStatusFailed, IconStatusIncomplete, IconStatusPassed, IconStatusSkipped, IconStatusTerminate, IconStopWatch, IconTrendDown, IconTrendDownGreen, IconTrendUp, IconTrendUpRed, IconUser } from '../../images/CustomIcon';
import moment from 'moment-timezone';
import 'moment-duration-format';
import Avatar from 'react-avatar';

export const ExecutionUtils = {
  getExecutionTarget: (row) => {
    return row.executionTestSuiteResources.map(({ urlId, testSuite, profiles, platform, status }) => ({
      executionId: row.order,
      targetName: testSuite ? testSuite.name : '',
      testSuiteName: testSuite ? testSuite.name : '',
      testSuitePath: testSuite ? testSuite.path : '',
      projectId: row.project.id,
      executionTestSuiteId: urlId,
      profiles,
      platform,
      status
    }));
  },
  
  hasTestPlan: (execution) => {
    const jobs = get(execution, 'jobs', []);
    return jobs.length > 0;
  },

  hasTestSuiteCollection: (execution) => {
    const testSuiteCollections = get(execution, 'testSuiteCollections', []);
    return testSuiteCollections.length > 0;
  },

  badgeComponent: (number) => {
    return (
      <div className='badge-number'>
        +{number}
      </div>
    )
  },
};

const ExecutionDecorator = {

  lineStatusDecorator: (row) => {
    const status = row.status.toUpperCase();
    let className;
    switch (status) {
      case 'PASSED':
        className = "line-status-passed";
        break;
      case 'FAILED':
        className = "line-status-failed";
        break;
      case 'INCOMPLETE':
        className = "line-status-incomplete";
        break;
      default:
        className = "line-status-none";
        break;
    }
    return <div className={className} />;
  },

  statusDecorator: (row) => {
    const status = row.status?.toUpperCase();
    let iconTitle = startCase(toLower(status));
    let statusIcon;
    switch (status) {
      case 'PASSED':
        statusIcon = <IconStatusPassed />;
        break;
      case 'FAILED':
        statusIcon = <IconStatusFailed />;
        break;
      case 'INCOMPLETE':
        statusIcon = <IconStatusIncomplete />;
        break;
      default:
        statusIcon = <CircularProgress />;
        break;
    }
    return <span className="icon-status" title={iconTitle}>{statusIcon}</span>;
  },

  nameDecorator: (row) => {
    let name;
    if (ExecutionUtils.hasTestPlan(row)) {
      const job = get(row, 'jobs', [])?.[0];
      name = job?.runConfiguration?.name;
    } else if (ExecutionUtils.hasTestSuiteCollection(row)) {
      const testSuiteCollections = get(row, 'testSuiteCollections', [])?.[0];
      name = testSuiteCollections?.name;
    } else {
      const executionTargets = ExecutionUtils.getExecutionTarget(row)?.[0];
      name = executionTargets?.testSuiteName;
    }
    return (
      <div>
        {name}
      </div>
    );
  },

  idDecorator: (row) => {
    const order = get(row, 'order');
    return (
      <div className="gap-detail-section">
        #{order}
      </div>
    );
  },

  startTimeDecorator: (row) => {
    const startTime = get(row, 'startTime');
    return (
      startTime && <div className="gap-detail-section">
        <IconClock className='margin-icon' />
        {moment.tz(startTime, '').format("MM/DD/YYYY HH:mm")}
      </div>
    );
  },

  durationDecorator: (row) => {
    const duration = get(row, 'elapsedDuration');
    let formatValue;
    if (duration < 1000) {
      formatValue = 'S[ms]';
    } else {
      formatValue = 'h[h] m[m] s[s]';
    }
    return (
      typeof(duration) === "number" && <div className="gap-detail-section">
        <IconStopWatch className='margin-icon' />
        {moment.duration(duration).format(formatValue)}
      </div>
    );
  },

  executionTimeDecorator: (row) => {
    const executionTime = get(row, 'totalDuration');
    return ExecutionDecorator.durationFormatDecorator(executionTime);
  },

  durationFormatDecorator: (time) => {
    if (time === 0 || time === undefined) return "-";
    let formatValue;
    if (time < 1000) {
      formatValue = 'S[ms]';
    } else {
      formatValue = 'h[h] m[m] s[s]';
    }
    return (
      moment.duration(time).format(formatValue)
    );
  },

  oSDecorator: (row) => {
    const executionTargets = ExecutionUtils.getExecutionTarget(row);
    const platforms = executionTargets.map((executionTarget) => executionTarget.platform);
    const oSNames = platforms.map((platform) => get(platform, 'osName', ''));
    const oSTypes = oSNames.map((oSName) => typeByOS(oSName)).filter((oSType) => (oSType !== ''));
    const uniqueOSTypes = uniq(oSTypes);
    return (
      uniqueOSTypes[0] && <div className="gap-detail-section">
        <span className="browser-and-os-icon">{iconByOS(uniqueOSTypes[0])}</span>
        {uniqueOSTypes.length > 1 && ExecutionUtils.badgeComponent(uniqueOSTypes.length - 1)}
      </div>
    );
  },

  browserDecorator: (row) => {
    const executionTargets = ExecutionUtils.getExecutionTarget(row);
    const platforms = executionTargets.map((executionTarget) => executionTarget.platform);
    const browserNames = platforms.map((platform) => get(platform, 'browserName', ''));
    const browserTypes = browserNames.map((browserName) => typeByBrowsers(browserName)).filter((browserType) => (browserType !== ''));
    const uniqueBrowserTypes = uniq(browserTypes);
    return (
      uniqueBrowserTypes[0] && <div className="gap-detail-section">
        <span className="browser-and-os-icon">{iconByBrowser(uniqueBrowserTypes[0])}</span>
        {uniqueBrowserTypes.length > 1 && ExecutionUtils.badgeComponent(uniqueBrowserTypes.length - 1)}
      </div>
    );
  },

  profileDecorator: (row) => {
    const executionTargets = ExecutionUtils.getExecutionTarget(row);
    const profiles = executionTargets.map((executionTargets) => executionTargets.profiles);
    if (isArray(profiles)) {
      const profileFlatten = flatten(profiles);
      const profileUnique = uniq(profileFlatten);
      return (
        profileUnique[0] && <div className="gap-detail-section">
          <IconProfile className='margin-icon' />
          {profileUnique[0]}
          {profileUnique.length > 1 && ExecutionUtils.badgeComponent(profileUnique.length - 1)}
        </div>
      );
      
    } else return (
      profiles && <div className="gap-detail-section">
        <IconProfile className='margin-icon' />
        {profiles}
      </div>);
  },

  asigneeDecorator: (row) => {
    const user = get(row, 'user');
    if (user?.avatar) {
      return (
        <img
          className="rounded-circle"
          width={16}
          height={16}
          src={user.avatar}
          title={user.fullName}
          alt={user.fullName}
        />
      )
    } else if (user?.fullName) {
      return (
        <Avatar
          className="rounded-circle"
          size={16}
          round={16}
          name={user.fullName}
          color={null}
          maxInitials={2}
      />
      )
    } else {
      return (
        <IconUser className="rounded-circle" />
      )
    }
  },

  trendDecorator: (row) => {
    if (row === 0) {
      return null;
    }
    const diffFloat = parseFloat(row).toFixed(2);
    let label;
    let className;
    let icon;
    if (diffFloat < 0) {
      label = `${Math.abs(diffFloat)}%`;
      icon = <IconTrendDown className='margin-icon' />
      className = 'trend-section trend-down';
    } else {
      label = `${Math.abs(diffFloat)}%`;
      icon = <IconTrendUp className='margin-icon' />
      className = "trend-section trend-up";
    }
    return (
      <span className={className}>
        {icon}
        {label}
      </span>
    );
  },

  trendDecoratorV2: (row) => {
    if (row === 0) {
      return null;
    }
    const diffFloat = parseFloat(row).toFixed(2);
    let label;
    let className;
    let icon;
    if (diffFloat < 0) {
      label = `${Math.abs(diffFloat)}%`;
      icon = <IconTrendDownGreen className='margin-icon' />
      className = 'trend-section trend-down-v2';
    } else {
      label = `${Math.abs(diffFloat)}%`;
      icon = <IconTrendUpRed className='margin-icon' />
      className = "trend-section trend-up-v2";
    }
    return (
      <span className={className}>
        {icon}
        {label}
      </span>
    );
  },

  statusBarTestRunDecorator: (row) => {
    const {
      totalPassedTests,
      totalFailedTests,
      totalErrorTests,
      totalIncompleteTests,
      totalSkippedTests,
    } = row;

    const payload = [
      {
        value: totalPassedTests,
        label: "Passed",
        icon: <IconStatusPassed />
      },
      {
        value: totalFailedTests,
        label: "Failed",
        icon: <IconStatusFailed />
      },
      {
        value: totalErrorTests,
        label: 'Error',
        icon: <IconStatusError className="icon-error-status-v2"/>
      },
      {
        value: totalIncompleteTests,
        label: 'Incomplete',
        icon: <IconStatusTerminate />
      },
      {
        value: totalSkippedTests,
        label: 'Skipped',
        icon: <IconStatusSkipped />
      },
    ];

    return (
      <div className="status-bar">
        <div className="status-bar-detail">
          {payload.map(({ label, value, icon }) => (
            <span className="status-bar-detail-result" key={label}>
              <span className="icon">
                {icon}
              </span>
              <span>
                {value}
              </span>
            </span>
          ))}
        </div>
      </div>
    );
  },
};

export default ExecutionDecorator;
