import {
  Button,
  Card,
  CardContent,
  makeStyles,
} from "@material-ui/core";
import {
  IconEmptySet,
} from "../../../../images/CustomIcon";
import KSActionService from "../../../../services/KSActionService";

const styles = makeStyles({
  cardContent: {
    display: "flex",
    flexDirection: "column",
    gap: "6px",
    padding: "40px !important",
    borderRadius: "4px",
    border: "solid 1px #dbdde5",
  },
  iconEmptySet: {
    marginBottom: "4px",
  },
  alignContent: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textAlign: "center",
  },
  errorText: {
    maxWidth: '368px',
    fontSize: "11px",
    color: "#727993",
    paddingBottom: "16px",
  },
  notifyText: {
    fontSize: "12px",
    fontWeight: "600",
    color: "#4d5369",
    marginBottom: "8px",
  },
  actionBtn: {
    fontSize: "12px",
    fontWeight: '400',
    marginLeft: "5px",
    textTransform: "none",
    color: "#fff",
    padding: "5px 12px",
    backgroundColor: "#22283c",
    '&:hover': {
      color: "#fff",
      backgroundColor: "#22283c",
    },
  },
});

export const ErrorCardComponent = ({elevation = 0, isRenderPlatformTitle = true}) => {
  const classes = styles();

  const handleOpenIntegration =  () => {
    KSActionService.openIntegrateWithPlatform();
  };

  return (
    <>
      {isRenderPlatformTitle && <div style={{ fontSize: "12px", color: "#727993", fontWeight: 600, marginBottom: "12px" }}>PLATFORM</div>}
      <Card elevation={elevation} className={classes.card}>
        <CardContent className={classes.cardContent}>
          <div className={classes.alignContent}>
            <div>
              <div><IconEmptySet className={classes.iconEmptySet}/></div>
              <div className={classes.notifyText}> Something went wrong </div>
              <div className={classes.errorText}>
                We are unable to retrieve your data at this time. Please restart the app or double-check the configuration settings.
              </div>
              <Button evelation={0} color="success" className={classes.actionBtn} onClick={handleOpenIntegration}>
                Integrate with Platform
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </>
  );
}
