import { IconChrome, IconChromeHeadless, IconChromium, IconEdge, IconEdgeChromium, IconFirefox, IconFirefoxHeadless, IconIE, IconSafari, IconUnknownFile } from "../../images/CustomIcon";

export const BROWSER_HEADLESS_SUFFIX = ' (headless)';

export const browserDetector = {
  isChrome: (browserName) => {
    return browserName.toLowerCase().includes('chrome');
  },

  isFirefox: (browserName) => {
    return browserName.toLowerCase().includes('firefox');
  },

  isIE: (browserName) => {
    return browserName.toLowerCase().includes('internet explorer') || browserName.toLowerCase().includes('ie');
  },

  isEdge: (browserName) => {
    return browserName.toLowerCase().includes('edge');
  },

  isSafari: (browserName) => {
    return browserName.toLowerCase().includes('safari');
  },

  isEdgeChromium: (browserName) => {
    return browserName.toLowerCase().includes('edge chromium');
  },

  isChromium: (browserName) => {
    return browserName.toLowerCase().includes('chromium');
  },

  isMsEdge: (browserName) => {
    return browserName.toLowerCase().includes('msedge');
  },

  isChromeHeadless: (browserName) => {
    return browserName?.toLowerCase().startsWith('chrome') && browserName?.toLowerCase().endsWith(BROWSER_HEADLESS_SUFFIX.toLowerCase());
  },

  isFirefoxHeadless: (browserName) => {
    return browserName?.toLowerCase().startsWith('firefox') && browserName?.toLowerCase().endsWith(BROWSER_HEADLESS_SUFFIX.toLowerCase());
  },
};

export const typeByBrowsers = (browserName = '') => {
    if (browserDetector.isChrome(browserName)) {
      return 'Chrome';
    } else if (browserDetector.isFirefox(browserName)) {
      return 'Firefox';
    } else if (browserDetector.isIE(browserName)) {
      return 'Internet Explorer';
    } else if (browserDetector.isEdgeChromium(browserName)) {
      return 'Edge Chromium';
    } else if (browserDetector.isEdge(browserName)) {
      return 'Edge';
    } else if (browserDetector.isSafari(browserName)) {
      return 'Safari';
    } else if (browserDetector.isChromium(browserName)) {
      return 'Chromium';
    } else if (browserName !== '') {
      return 'Others';
    }
    return '';
}

export const iconByBrowser = (browserName = '') => {
    if (browserDetector.isChromeHeadless(browserName)) {
      return <IconChromeHeadless />;
    } else if (browserDetector.isFirefoxHeadless(browserName)) {
      return <IconFirefoxHeadless />;
    } else if (browserDetector.isChrome(browserName)) {
      return <IconChrome />;
    } else if (browserDetector.isFirefox(browserName)) {
      return <IconFirefox />;
    } else if (browserDetector.isIE(browserName)) {
      return <IconIE />;
    } else if (browserDetector.isEdgeChromium(browserName) || browserDetector.isMsEdge(browserName)) {
      return <IconEdgeChromium />;
    } else if (browserDetector.isEdge(browserName)) {
      return <IconEdge />;
    } else if (browserDetector.isSafari(browserName)) {
      return <IconSafari />;
    } else if (browserDetector.isChromium(browserName)) {
      return <IconChromium />;
    } else if (browserName === 'Others') {
      return <IconUnknownFile />;
    }
    return '';
}
