import { IconAndroid, IconIOS, IconLinux, IconMac, IconUnknownFile, IconWindows } from "../../images/CustomIcon";

export const BROWSER_HEADLESS_SUFFIX = ' (headless)';

export const oSDetector = {
  isWindows: (osName) => {
    return osName.toLowerCase().includes('windows');
  },

  isLinux: (osName) => {
    return osName.toLowerCase().includes('linux');
  },

  isMacOS: (osName) => {
    return osName.toLowerCase().includes('mac') || osName.toLowerCase().includes('darwin');
  },

  isIOS: (osName) => {
    return osName.toLowerCase().includes('ios');
  },

  isAndroid: (osName) => {
    return osName.toLowerCase().includes('android');
  }
};

export const typeByOS = (osName = '') => {
    if (oSDetector.isWindows(osName)) {
      return 'Windows';
    } else if (oSDetector.isAndroid(osName)) {
      return 'Android';
    } else if (oSDetector.isIOS(osName)) {
      return 'iOS';
    } else if (oSDetector.isLinux(osName)) {
      return 'Linux';
    } else if (oSDetector.isMacOS(osName)) {
      return 'MacOS';
    } else if (osName !== '') {
      return 'Others';
    }
    return '';
};

export const iconByOS = (osName = '') => {
  if (oSDetector.isWindows(osName)) {
    return <IconWindows />;
  } else if (oSDetector.isLinux(osName)) {
    return <IconLinux />;
  } else if (oSDetector.isMacOS(osName)) {
    return <IconMac />;
  } else if (oSDetector.isIOS(osName)) {
    return <IconIOS />;
  } else if (oSDetector.isAndroid(osName)) {
    return <IconAndroid />;
  } else if (osName === 'Others') {
    return <IconUnknownFile />;
  }
  return '';
}
